.lead-info-card>.card-menubar {
	position: relative;
}
.lead-info-card>.card-content {
	display: grid;
	grid-template-columns: 50% 50%;
	align-items: center;
	justify-content: center;
	height: calc(100% - 40px);
}
.lead-sum-form-item {
	width: 95%;
	display: grid;
	grid-template-columns: 45% 55%;
	align-items: center;
	justify-content: center;
	padding-top:5px;
	padding-bottom:5px;
	gap: 0px;
	font-size: 1.5rem;
	padding-left: 10px;
	padding-right: 10px;
	border-radius: 10px;
	border: 1px solid transparent;
}
.lead-sum-form-item:hover {
	border: 1px solid #ccc;
	background-color: #e5e5e5;
}
.lead-sum-form-item>.form-info-title {
	font-weight: 600;
}
.lead-sum-form-item>div {
	font-style: italic;
	color: #333333;
	text-align: end;
}
.lead-sum-form-item#revenue-cont>div::before,
.lead-sum-form-item#rpo-cont>div::before {
	content: "$";
}
.lead-sum-form-item#closepercent-cont>div::after {
	content: "%";
}
/*Adjust lead summary form when viewed in medium cards*/
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content>.lead-sum-form-item {
	font-size: larger;
}
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content {
	display: grid;
	grid-template-columns: 50% 50%;
}