/*Tracker Card Base CSS*/
#tracker-month-view {
	min-width: 100%;
	max-width: 100%;
	overflow: hidden;
}
#tracker-month-view>.card-menubar {
	position: relative;
}

/*Menu position/width*/
#tracker-month-view>.card-content>.menu-bar {
	position: relative;
	margin: 0px;
	padding: 0px;
	width: 100%;
	top: 0px;
	border-bottom: 1px solid #ccc;
}
.medium-cards>#tracker-view-cont>#tracker-month-view>.card-content>.menu-bar>.menu-button {
	font-size: 10px;
}
/*Menu button color*/
#tracker-month-view>.card-content>.menu-bar>.menu-button {
	background-color: #103b61;
	font-size: 13px;
}
#tracker-month-view>.card-content>.menu-bar>.menu-button.selected {
	background-color: #062a49;
}
#tracker-month-view>.card-content>.menu-bar>.menu-button:hover {
	background-color: #062a49;
}
/*Remove padding from table*/
#tracker-month-view-card-content {
	height: calc(100% - 110px);
}
#tracker-month-view-card-content>.card-content {
	padding: 0px;
	min-height: 100%;
	max-height: 100%;
	height: 100%;
	overflow-y: auto;
}
/*Table row grid*/
#tracker-month-view-card-content>.tablerow.header,
#tracker-month-view-card-content>.card-content>.tablerow {
	grid-template-columns: repeat(5, 20%);
	padding: 0px;
	line-height: 40px;
}
/*Border bottom on table row*/
#tracker-month-view-card-content>.card-content>.tablerow {
	border-bottom: 1px solid #e1e1e1;
}
/*Table row Headers & Table Rows*/
#tracker-month-view-card-content>.tablerow.header {
	background-color: #f0f0f0;
	color: #212121;
	border-bottom: 1px solid #ccc;
	font-weight: 600;
	font-size: larger;
	line-height: 40px;
}
/*Reset padding/margin/bg color*/
#tracker-month-view-card-content>.tablerow.header>div,
#tracker-month-view-card-content>.card-content>.tablerow>div  {
	background-color: transparent;
	margin: 0px;
	padding: 0px;
}
/*Table row hover*/
#tracker-month-view-card-content>.card-content>.tablerow:hover {
	background-color: #efefef;
	cursor: pointer;
}
/*Header borders*/
#tracker-month-view-card-content>.tablerow.header>div {
	border-right: 1px solid #ccc;
}
#tracker-month-view-card-content>.tablerow.header>:last-child {
	border-right: 0px;
}