.App {
  text-align: center;
}

.bg-light-gray {
	background-color: #f9f9f9;
}

.app-container {
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
}
#main-app {
	position: absolute;
	top: 40px;
	height: calc(100% - 40px);
}
#main-app>:last-child {
	margin-bottom: 20px;
}
.app-container.medium-cards {
	display: grid;
	grid-template-columns: 49.5% 49.5%;
	gap: 1%;
	row-gap: 40px;
	height: calc(100vh - 60px);
	min-height: calc(100vh - 60px);
	max-height: calc(100vh - 60px);
}
.app-container.list {
	display: flex;
	flex-direction: column;
	gap: 5%;
}

.card.no-padding>.card-content {
	padding: 0px;
}
.menu-bar-custom {
	background-color: #062a49;
	position: absolute;
	top:0px;
	width: 60px;
	height: 100vh;
}

.outline-card {
	border: 1px solid #ccc;
	filter: unset;
}

.no-padding {
	padding: 0px;
}

.icon-button {
	background-color: transparent;
	filter: unset;
	width: fit-content;
}
.icon-button:hover {
	filter: var(--drop-shadow);
	background-color: transparent;
}