#tracker-view-cont {
	height: calc(100vh - 80px);
	min-height: calc(100vh - 80px);
	max-height: calc(100vh - 80px);
	width: 100%;
	display: flex;
	flex-direction: column-reverse;
	grid-template-columns: 70% 27.5%;
	gap: 1%;
}

#lead-summary-card {
	min-width: 100%;
	min-height: 30%;
	max-height: 30%;
}
#tracker-month-view {
	min-width: 100%;
	min-height: 69%;
	max-height: 69%;
}

#lead-summary-card>.card-content {
	display: grid;
	grid-template-columns: 49.5% 49.5%;
	gap: 1%;
	height: calc(100% - 40px);
}

.lead-info-card {
	min-height: 100%;
	max-height: 100%;
}